.timeline-list-item-content {
  /* box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); */
  /* border-radius: 5px; */
  //background-color: #fff;
  display: flex;
  flex-direction: column;
  margin-left: 7%;
  position: relative;
  padding: 5px 5px 10px 5px;
}

.time {
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 130%;
  text-align: right;
  color: #101010;
  opacity: 0.4;
}

.timeline-list-item-content:before {
  content: '';
  //background: #d4d9df;
  display: inline-block;
  position: absolute;
  left: -30px;
  width: 2px;
  height: 100%;
  //z-index: 400;
  opacity: 0.2;
  border: 1px dashed #101010;
}

.last-time-line-content:before{
  border: none !important;
}
.timeline-list-item-content .circle {
  //box-shadow: 0 0 0 10px rgba(35, 179, 232, 0.2);
  //background-color: #22c0e8;
  //border: 3px solid #22c0e8;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 20px);
  //top: 10%;
  width: 10px;
  height: 10px;
  //z-index: 100;
  right: auto;
  left: -35px;
}

@media only screen and (max-width: 1023px) {
  .timeline-list-content {
    max-width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .timeline-list-content,
  .timeline-list:nth-child(odd) .timeline-list-content {
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }

  .timeline-list-content .tag {
    width: calc(100% - 10px);
    text-align: center;
  }

  .timeline-list-content time {
    margin-top: 20px;
  }

  .timeline-list-content a {
    text-decoration: underline;
  }

  .timeline-list-content a::after {
    display: none;
  }
}